import { cilPenAlt, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CFormLabel,
  CFormSelect,
  CFormTextarea,
  CRow,
} from '@coreui/react';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { api, headers } from 'src/views/api';

const AddCategory = () => {
  const [categories, setCategoeies] = useState([]);
  const [mainCategory, setMainCategory] = useState(null);
  const [selectedCategory, setSelectedCategory] = useState({
    type: '',
    id: '',
  });
  const [category, setCategory] = useState({
    name: '',
    description: '',
  });
  const [oneCategory, setOneCategory] = useState(null);
  const role = localStorage.role;
  const userId = localStorage.userId;

  useEffect(async () => {
    const response = await axios.get(`${api}/api/category`);
    setCategoeies(response?.data);
  }, []);

  const handleChange = (e) =>
    setCategory({ ...category, [e.target.name]: e.target.value });

  const handleSumbit = async () => {
    try {
      let response;
      if (mainCategory && mainCategory !== 'انتخاب') {
        response = await axios.post(
          `${api}/api/sub-category`,
          { ...category, category: mainCategory },
          {
            headers,
          }
        );
      } else {
        response = await axios.post(`${api}/api/category`, category, {
          headers,
        });
      }

      if (response?.status === 200) {
        const response = await axios.get(`${api}/api/category`);
        setCategoeies(response?.data);
        setCategory({
          name: '',
          description: '',
        });
        setMainCategory('انتخاب');
      }
    } catch (error) {
      alert(
        error?.response?.data?.message
          ? error?.response?.data?.message
          : 'نام تکراری مجاز نمیباشد'
      );
    }
  };

  const handleDeleteCategory = async (id) => {
    const confirm = window.confirm(
      'از حذف این دسته اطمینان دارید؟ با حذف دسته مادر کلیه زیر دسته های آن دسته حذف خواهد شد'
    );

    if (confirm) {
      try {
        const response = await axios.delete(`${api}/api/category/${id}`, {
          headers,
        });

        if (response?.status === 200) {
          if (role === 'admin') {
            const response = await axios.get(`${api}/api/category/${userId}`);
            setCategoeies(response?.data);
          } else {
            const response = await axios.get(`${api}/api/category`);
            setCategoeies(response?.data);
          }
        }
      } catch (error) {
        alert(error?.response?.data?.message);
      }
    }
  };

  const handleUpdate = async () => {
    if (selectedCategory?.type === 'main') {
      await axios.put(`${api}/api/category/${selectedCategory?.id}`, category, {
        headers,
      });
    } else {
      await axios.put(
        `${api}/api/sub-category/${selectedCategory?.id}`,
        {
          ...category,
          category: mainCategory,
        },
        {
          headers,
        }
      );
    }

    await setMainCategory('انتخاب');
    await setCategory({
      name: '',
      description: '',
    });
    await setOneCategory({
      name: '',
      description: '',
    });

    const response = await axios.get(`${api}/api/category`);
    await setCategoeies(response?.data);
  };

  return (
    <div>
      <CContainer>
        <CRow>
          <CCol sm={12} md={6}>
            <div>
              {categories?.length ? (
                <div>
                  <ul style={{ margin: 0, padding: 0 }}>
                    {categories?.map((el) => (
                      <>
                        <li
                          style={{
                            padding: 5,
                            background: '#f9f9f9',
                            marginBottom: 5,
                            listStyle: 'none',
                            borderRadius: 5,
                            border: '1px solid #999',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                          }}
                        >
                          <span>
                            {el?.name}{' '}
                            <span
                              style={{
                                fontSize: 12,
                                color: '#999',
                                margin: '0 10px',
                              }}
                            >
                              {`(${el?.description})`}
                            </span>
                          </span>
                          <span>
                            <span
                              style={{
                                cursor: 'pointer',
                                color: 'red',
                                margin: '0 10px',
                              }}
                              onClick={() => handleDeleteCategory(el?._id)}
                            >
                              <CIcon icon={cilTrash} />
                            </span>
                            <span
                              style={{
                                cursor: 'pointer',
                                color: 'blue',
                                margin: '0 10px',
                              }}
                              onClick={async () => {
                                await setSelectedCategory({
                                  type: 'main',
                                  id: el?._id,
                                });

                                const cat = await axios.get(
                                  `${api}/api/category-one/${el?._id}`
                                );
                                setMainCategory('انتخاب');
                                setOneCategory(cat?.data);
                              }}
                            >
                              <CIcon icon={cilPenAlt} />
                            </span>
                          </span>
                        </li>
                        {el?.subCategories?.length ? (
                          <ul>
                            {el?.subCategories?.map((e) => (
                              <li
                                style={{
                                  padding: '5px 0',
                                  display: 'flex',
                                  alignItems: 'center',
                                  justifyContent: 'space-between',
                                  padding: 5,
                                }}
                              >
                                <span>
                                  {e?.name}{' '}
                                  <span
                                    style={{
                                      fontSize: 12,
                                      color: '#999',
                                      margin: '0 10px',
                                    }}
                                  >
                                    {`(${e?.description})`}
                                  </span>
                                </span>
                                <span>
                                  <span
                                    style={{
                                      cursor: 'pointer',
                                      color: 'red',
                                      margin: '0 10px',
                                    }}
                                  >
                                    <CIcon icon={cilTrash} />
                                  </span>
                                  <span
                                    style={{
                                      cursor: 'pointer',
                                      color: 'blue',
                                      margin: '0 10px',
                                    }}
                                    onClick={async () => {
                                      await setSelectedCategory({
                                        type: 'sub',
                                        id: e?._id,
                                      });

                                      const cat = await axios.get(
                                        `${api}/api/sub-category/${e?._id}`
                                      );

                                      setMainCategory(cat?.data?.category);
                                      setOneCategory(cat?.data);
                                    }}
                                  >
                                    <CIcon icon={cilPenAlt} />
                                  </span>
                                </span>
                              </li>
                            ))}
                          </ul>
                        ) : null}
                      </>
                    ))}
                  </ul>
                </div>
              ) : (
                'دسته بندی یافت نشد'
              )}
            </div>
          </CCol>
          <CCol>
            <CForm>
              <div className='mb-3'>
                <CFormLabel htmlFor='categoryName'>نام دسته بندی</CFormLabel>
                <CFormInput
                  type='text'
                  id='categoryName'
                  placeholder='نام دسته بندی را اینجا وارد کنید'
                  name='name'
                  onChange={handleChange}
                  value={category.name || oneCategory?.name}
                />
              </div>
              {selectedCategory?.type === 'main' ? null : (
                <div className='mb-3'>
                  <CFormLabel htmlFor='categoryName'>دسته بندی مادر</CFormLabel>
                  <CFormSelect
                    options={[
                      'انتخاب',
                      ...categories?.map((el) => {
                        return {
                          label: el?.name,
                          value: el?._id,
                        };
                      }),
                    ]}
                    value={mainCategory}
                    onChange={(e) => setMainCategory(e.target.value)}
                  />
                </div>
              )}
              <div className='mb-3'>
                <CFormLabel htmlFor='categoryDescription'>
                  توضیحات دسته بندی
                </CFormLabel>
                <CFormTextarea
                  id='categoryDescription'
                  rows='3'
                  name='description'
                  onChange={handleChange}
                  value={category.description || oneCategory?.description}
                ></CFormTextarea>
              </div>
              <div className='mb-3'>
                <CButton
                  color='primary'
                  onClick={() =>
                    selectedCategory?.id ? handleUpdate() : handleSumbit()
                  }
                >
                  {!selectedCategory?.id ? 'افزودن' : 'ویرایش'}
                </CButton>
                {selectedCategory?.id && (
                  <CButton
                    style={{ marginRight: 10 }}
                    color='secondary'
                    onClick={() => {
                      setSelectedCategory({ type: '', id: '' });
                      setOneCategory({
                        name: '',
                        description: '',
                      });
                      setMainCategory('انتخاب');
                    }}
                  >
                    خروج از حالت ویرایش
                  </CButton>
                )}
              </div>
            </CForm>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};

export default AddCategory;
